<template>
  <MainPage v-if="page=='mainpage'"
   @turnToClass="turnToClass"
   @turnToSet="turnToSet"
   @turnToMenu="turnToMenu"/>
   <h1 v-if="page=='classpage'">클래스 페이지</h1>
   <SetPage v-if="page=='setpage'"/>
</template>

<script>
//! This thing is made for 360x780 window, but use vh and vw value instead of px value to ensure flexiblity

import MainPage from '@/components/mainpage/mainpage.vue';
import SetPage from '@/components/setpage/setpage.vue';
import files from '@/assets/words/files.json';
import Swal from 'sweetalert2';

const fileList = files;

import first from '@/assets/words/firstWords.json';
import { faBullseye } from '@fortawesome/free-solid-svg-icons';

const firstJSON = first;

const ToastToPage = Swal.mixin({
  toast: true,
  position:'top-end',
  showConfirmButton: true,
  timerProgressBar: true,
  /* didOpen: (toast) => {
    toast.addEventListener('mouseenter', Swal.stopTimer)
    toast.addEventListener('mouseleave', Swal.resumeTimer)
  } */
})

const Toast2 = Swal.mixin({
  toast:true,
  position:'top-end',
  showConfirmButton:true,
  timerProgressBar: true
})

Swal.fire({
  icon:'info',
  title:'WordWise에 오신 걸 환영합니다!',
  text:'WordWise는 아직 버그가 있을 수 있습니다! 버그는 Github에서 WordWise-project로 제보해주세요!',
  showCancelButton:false,
  allowOutsideClick:false
}) .then(result => {
  if(result.isConfirmed) {
    Toast2.fire({
      icon:'success',
      title:'안내합니다!',
      text:'WordWise는 휴대전화로 접속하시길 권장드립니다. 화면 크기가 올바르지 않을 시 UI에 오류가 생길 수 있습니다.',
      showConfirmButton:'true'
    });
  }
});

export default {
  name: 'App',
  components: {
    MainPage,
    SetPage
  },
  data() {
    return{
      page: 'mainpage',
      publicPath: process.env.BASE_URL,
      fileList,
      firstJSON,
    };
  },
  methods:{
    turnToClass: function(){
      this.page = 'mainpage';
      ToastToPage.fire({
        icon:'success',
        title: 'Successfully entered Class Menu!'
      });
    },
    turnToSet: function(){
      this.page = 'setpage';
      ToastToPage.fire({
        icon:'success',
        title: 'Successfully entered Set Menu!',
        text: '단어장 내에서 끝까지 스크롤을 내리면 메뉴바가 있습니다! 귀찮으신 건.. 아니죠?',
      });
    },
    turnToMenu: function(){
      this.page = 'mainpage'
      ToastToPage.fire({
        icon:'success',
        title: 'Successfully entered Main Menu!'
      });
    }
  }
}
</script>

